(function(){
  'use strict';
    angular.module('maintenence', [])
      .controller('maintenenceCtrl', Maintenence);

    /** @ngInject */

    function Maintenence(GardenService, toastr, $state){
      var vm = this;
      vm.selection = null;
      vm.selectionFilter = Selection.bind(vm);
      vm.editor = Editor.bind(vm);
      function list(){
        GardenService.maintenence('gartenumaenderung').then(function(response){
          vm.lists = response.data.data.data;
        })
      }

      function Editor(id){
        if (!vm.selection){
          vm.selection = id;
        }
        $state.go('edit', {id: vm.selection});
      }

      function Selection(){
        if (vm.selection){
          vm.selected = vm.lists.filter(function(l){
            return l.id == vm.selection;
          });
          if (vm.selected.length > 0){
            GardenService.setStorage(vm.selected);
          }
          return;
        }
        toastr.info('Please select the garden');

      }

      list();
    }



})();

(function(){
  'use strict';
    angular.module('gardeningApp')
      .config(Config);

    function Config($stateProvider){
      $stateProvider.state('edit',{
        url: '/edit/:id',
        controller: 'editCtrl',
        controllerAs: 'edit',
        templateUrl: 'app/edit/edit.html',
        resolve:{
          appData: function(GardenService, $stateParams, toastr, $q, $location){
            var defer = $q.defer();
            GardenService.getGardenById($stateParams.id).then(function(data){
              defer.resolve(data.data.data);
            }, function(err){
              toastr.info('Not Found');
              $location.url('/');
            })
            return defer.promise;
          }
        }
      })
    }


})();

